<template>
  <div class="ordersummary">
    <el-row class="tac">
      <el-col :span="3">
        <el-menu
          default-active="1"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-submenu
            v-for="(item, idx) in titlelist"
            :key="idx"
            :index="idx + 1 + ''"
          >
            <template slot="title">
              <i class="el-icon-location"></i>
              <span>{{ item }}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item class="default_active" :index="idx + 1 + '-1'">{{
                item + "SKU订单汇总"
              }}</el-menu-item>
              <el-menu-item :index="idx + 1 + '-2'">{{
                item + "SPU订单汇总"
              }}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-col>
      <el-col :span="21">
        <div class="table_contain">
          <div class="table">
            <div class="row" style="width:200px;">
              <div>SPU</div>
              <div
                v-for="(item, index) in spu.value"
                :key="index"
                :style="{ background: item.color }"
              >
                <span v-if="item.value">{{ item.value }}</span>
                <span v-else>{{ item }}</span>
              </div>
            </div>
            <div class="row" v-if="sku" style="width:200px;">
              <div>SKU</div>
              <div
                v-for="(item, index) in sku.value"
                :key="index"
                :style="{ background: item.color }"
              >
                {{ item.value }}
              </div>
            </div>
            <div class="row">
              <div>FBA</div>
              <div
                v-for="(item, index) in fba.value"
                :key="index"
                :style="{ background: item.color }"
              >
                <span v-if="item.value === undefined">{{ item }}</span>
                <span v-else>{{ item.value }}</span>
              </div>
            </div>
            <div class="row">
              <div>FBM</div>
              <div
                v-for="(item, index) in fbm.value"
                :key="index"
                :style="{ background: item.color }"
              >
                <span v-if="item.value === undefined">{{ item }}</span>
                <span v-else>{{ item.value }}</span>
              </div>
            </div>
            <div class="row">
              <div>总计</div>
              <div
                v-for="(item, index) in zj.value"
                :key="index"
                :style="{ background: item.color }"
              >
                <span v-if="item.value === undefined">{{ item }}</span>
                <span v-else>{{ item.value }}</span>
              </div>
            </div>
            <div class="row" v-for="(items, index) in datalist" :key="index">
              <div>{{ items.name }}</div>
              <div
                v-for="(item, idx) in items.value"
                :key="idx"
                :style="{ background: item.color }"
              >
                {{ item.value }}
              </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      titlelist: [
        "胤佑美国",
        "爱瑙美国",
        "京汇欧洲",
        "京汇英国",
        "中睿欧洲",
        "中睿英国",
        "胤佑加拿大",
        "胤佑日本",
        "胤佑澳洲",
        "胤佑墨西哥",
        "东南亚"
      ],
      spu: {},
      link: [],
      fba: {},
      datalist: [],
      fbm: {},
      sku: {},
      zj: {}
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      const keyArr = key.split("-");
      console.log(keyArr);
      let firstIdx = parseInt(keyArr[0]) - 1;
      let secIdx = parseInt(keyArr[1]) - 1;
      console.log(secIdx);
      let _name = secIdx === 0 ? "SKU订单汇总" : "SPU订单汇总";
      let name = this.titlelist[firstIdx] + _name;
      let area = "";
      switch (firstIdx) {
        case 0:
          area = "yy_us";
          break;
        case 1:
          area = "an_us";
          break;
        case 2:
          area = "jh_es"; // eu改成es
          break;
        case 3:
          area = "jh_uk";
          break;
        case 4:
          area = "zr_es"; // eu改成es
          break;
        case 5:
          area = "zr_uk";
          break;
        case 6:
          area = "yy_ca";
          break;
        case 7:
          area = "yy_jp";
          break;
        case 8:
          area = "yy_au";
          break;
        case 9:
          area = "yy_mx";
          break;
        case 10:
          area = "dny";
          break;
      }
      console.log(name, area);

      // 发送请求
      this.getTableData(name, area);
    },
    getTableData(name, area) {
      this.$axios
        .get(this._url2 + "server/databases/get_sku_order_summary/", {
          params: {
            name,
            area
          }
        })
        .then(res => {
          console.log(res);
          this.spu = res.data.spu;
          this.link = res.data.link;
          this.fba = res.data.fba;
          this.datalist = res.data.data_list;
          this.sku = res.data.sku;
          this.zj = res.data.zj;
          this.fbm = res.data.fbm;
        });
    }
  },
  created() {
    // 发送请求
    this.getTableData("胤佑美国SKU订单汇总", "yy_us");
  }
};
</script>

<style>
.ordersummary .tac {
  min-width: 196px;
}
</style>
<style scoped>
.table {
  display: flex;
}
.table > .row {
  float: left;
  /* width: 60px; */
  text-align: right;
}
.row > div {
  border: 1px solid #e5e5e5;
  min-height: 30px;
  line-height: 28px;
  font-size: 14px;
  white-space: nowrap;
  padding: 0 5px;
}
.table_contain {
  width: 100%;
}
#app {
  overflow: scroll;
}
.default_active {
  color: burlywood;
}
</style>



